import {Component} from '@angular/core';
import {ROUTER_DIRECTIVES, provideRouter, Router} from '@angular/router';

import {DashboardComponent} from './+dashboard';
import {SocialComponent} from './+social';
import {AppLayoutsComponent} from "./+smartadmin/+app-layouts/app-layouts.component";
import {PrebuiltSkinsComponent} from "./+smartadmin/+prebuilt-skins/prebuilt-skins.component";

import {
  HeaderComponent, FooterComponent, NavigationComponent,
  DemoStatesComponent, RibbonComponent
} from "./shared";
import {Store} from "@ngrx/store";

export interface AppState {
  navigation:any
}


@Component({
  moduleId: module.id,
  selector: 'smartadmin-app',
  templateUrl: 'smartadmin.component.html',
  directives: [ROUTER_DIRECTIVES,
    HeaderComponent, FooterComponent, NavigationComponent, DemoStatesComponent, RibbonComponent],
  providers: [provideRouter([
      {path: '/dashboard', component: DashboardComponent},
      {path: '/social', component: SocialComponent},
      {path: '/smartadmin/app-layouts', component: AppLayoutsComponent},
      {path: '/smartadmin/prebuilt-skins', component: PrebuiltSkinsComponent},
    ]
  )]
})
export class SmartadminAppComponent {
  title = 'smartadmin works y!';

  constructor(public store:Store<AppState>,
              private router:Router) {

    store.subscribe((state)=> {
      console.log('state', state)
    })
  }
}
